html {
  line-height: 1.15;
}

body {
  margin: 0;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

a {
  background-color: transparent;
  color: inherit;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: "PT Sans", "Arial", sans-serif;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  outline-offset: -2px;
}

::-webkit-file-upload-button {
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

button:active,
button:focus {
  outline: none;
}

@font-face {
  font-family: "MinionPro";
  src: local("PT Sans"), url("../font/MinionPro-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "MinionPro";
  src: local("PT Sans"), url("../font/MinionPro-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
/* colors */
/* gradients */
/* vars */
/* keyframes */
@keyframes cloud-animation {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}
@keyframes aitf {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}
* {
  box-sizing: border-box;
}

.page__body {
  font-family: "MinionPro", sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  background-color: #000000;
  background-image: url("../img/shrift.png");
}

.container {
  height: 100%;
  max-width: 1600px;
  padding: 0 20px;
}
@media (min-width: 769px) and (max-width: 1200px) {
  .container {
    width: 100%;
  }
}

img {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

.wrapper {
  box-shadow: 10px 0 19px -17px #000000, -10px 0 19px -17px #000000;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  width: 1600px;
  margin: 0 auto;
  background-image: url("../img/main-bg.png");
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: center center;
}

body,
html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000000;
  margin: 0;
}

a {
  text-decoration: none;
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.nav-list {
  transition: all 0.2s ease-in-out;
  position: relative;
  display: flex;
  height: 209px;
  align-items: center;
  padding: 0 0 30px 0;
  margin: 30px 0 0 0;
}
.nav-list__logo {
  margin: -9px 0 0 0;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  display: block;
  width: 335px;
  height: 200px;
  background-image: url("../img/logo.png");
  background-size: 100% 100%;
  transition: all 0.2s ease-in-out;
}
.nav-list__logo:hover {
  background-image: url("../img/logo-hover.png");
  transition: all 0.2s ease-in-out;
}
.nav-list__items {
  margin: 0 40px 0 auto;
  height: fit-content;
  list-style-type: none;
  text-transform: uppercase;
  display: flex;
  column-gap: 50px;
  width: fit-content;
  font-weight: bold;
  color: white;
  letter-spacing: 3px;
  font-size: 20px;
}
.nav-list__item a {
  padding: 0 0 30px 0;
  border-bottom: 5px transparent solid;
  transition: all 0.2s ease-in-out;
}
.nav-list__item:hover a {
  border-bottom: 5px white solid;
}
.nav-list__networks-list {
  display: flex;
  list-style-type: none;
  column-gap: 30px;
}
.nav-list__networks-list-item {
  transition: all 0.5s ease-in-out;
}
.nav-list__networks-list-item svg g path {
  fill: white;
  transition: all 2s ease;
}
.nav-list__networks-list-item:hover {
  transform: scale(125%);
  transition: all 1s ease;
}
.nav-list__networks-list-item:hover svg g path {
  fill: black;
  transition: all 1s ease;
}

.hero__inner {
  min-height: 600px;
  margin: 80px 5% 0 5%;
  background-image: url("../img/svg/hero-cloud.svg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  transition: all 0.5s ease-in-out;
}
.hero__inner:hover {
  transform: scale(1.03);
  transition: all 0.5s ease-in-out;
}

.hero__inner-title {
  font-size: 32px;
  text-align: center;
  padding: 185px 0 0 131px;
  line-height: 2.5;
  letter-spacing: 5px;
}

.hero__inner-title span {
  font-size: 87px;
  text-transform: uppercase;
  line-height: 1.5;
  letter-spacing: 2px;
  background: url("../img/bg-gradient.png") repeat-y;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: aitf 80s linear infinite;
  -webkit-transform: translate3d(0, 0, 0);
  -webkit-backface-visibility: hidden;
  transform: scale(1, 1.5);
  display: block;
}