.nav-list {
  transition: $transition-fast;
  position: relative;
  display: flex;
  height: 209px;
  align-items: center;
  padding: 0 0 30px 0;
  margin: 30px 0 0 0;
  // background: $header-gradient, url("../img/scissors.png");
  // background-position: bottom 1px center, bottom 1px left 1px;
  // background-repeat: no-repeat;
  // background-size: 100% 4px, 50px 30px;
  
  &__logo {
    margin: -9px 0 0 0;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    display: block;
    width: 335px;
    height: 200px;
    background-image: url("../img/logo.png");
    background-size: 100% 100%;
    transition: $transition-fast;
  }

  &__logo:hover {
    background-image: url("../img/logo-hover.png");
    transition: $transition-fast;
  }
  
  &__items {
    margin: 0 40px 0 auto;
    height: fit-content;
    list-style-type: none;
    text-transform: uppercase;
    display: flex;
    column-gap: 50px;
    width: fit-content;
    font-weight: bold;
    color: white;
    letter-spacing: 3px;
    font-size: 20px;
  }
  
  &__item a {
    padding: 0 0 30px 0;
    border-bottom: 5px transparent solid;
    transition: $transition-fast;
  }
  
  &__item:hover a {
    border-bottom: 5px white solid;
  }
  
  &__networks-list {
    display: flex;
    list-style-type: none;
    column-gap: 30px;
  }
  
  
  &__networks-list-item {
    transition: all 0.5s ease-in-out;
    
  }
  

  &__networks-list-item svg g path {
    fill: white;
    transition: all 2s ease;
  }

  &__networks-list-item:hover {
    transform: scale(125%);
    transition: all 1s ease;
    
    
  }
  
  &__networks-list-item:hover svg g path {
    fill: black;
    transition: all 1s ease;
  }
  
}
